import styled from 'styled-components';
import {
  Row,
  Col,
  FormGroup,
  Label,
  Container,
} from 'reactstrap';
import axios from 'axios';
import TextareaAutosize from 'react-textarea-autosize';
import Page from '../../../components/tuDien/page'
import * as GA from "../../../utils/analystics";
import Loading from '../../../components/tuDien/loader';
import Layout from '../../../components/tuDien/layout'
import { replaceAll } from '../../../utils/utils';

function getHanViet(dict, chinesse) {
  let result = '', i =0, hanViet;
  let missing,mean;
  for (i=0;i<chinesse.length;i++) {
    mean = dict[chinesse[i]];
    if(mean){
      hanViet = mean["hv"].trim();
      if (hanViet.indexOf(' ')>0) {
        hanViet = '('+hanViet+')';
      }
    } else {
      hanViet ='';
      missing += ',' + chinesse[i];
    }
    result += hanViet + ' ';
  }
  if(missing) {
    console.log('Từ điển thiếu', missing);
  }
  return result.trim();
}

export default class extends Page {
  static async getInitialProps({req, query}) {
    super.getInitialProps({req, query});
    let props = await super.getInitialProps({req})
    // props.word = query.id
    return props
  }

  constructor(props) {
    super(props);

    const { words } = props;

    this.state = {
      loading: true,
      content: '',
      chinesse: '喂(wèi) sợ\n羡慕(xiànmù)： Ngưỡng mộ\n呈(chéng)現：lộ ra, phơi bày, thể hiện, biểu diễn\n面(miàn)前(qián): trước mặt, trước mắt, phía trước',
      hanViet: '',
      bacKinh: '',
      tuMois: '',
      dict: {},
    };
    axios.get('https://raw.githubusercontent.com/thanglamhuu/nddhp.github.io/master/dictClient.json').then((res) => {
      this.setState({
        dict: res.data,
        loading: false,
      });
    });
    this.onChangeHandlerChuHan = this.onChangeHandlerChuHan.bind(this);
  }

  onChangeHandlerChuHan(e) {
    const {dict} = this.state;
    const chinesse = e.target.value;
    let chuHans = [];
    const words = chinesse.split('\n');
    let means=[], word='', chuHan='', hanViet='', bacKinh='', tuHaiBen='';
    let j=0;
    let tuMois = '';

    let i=0;
    for (i=0;i<words.length;i++) {
      try {
        word='', chuHan='', hanViet='', bacKinh='';
        word = words[i].trim();
        //視(shì)之(zhī)自(zì)然(rán) nhìn thấy bình thường
        word = replaceAll(word,'：',':' );
        word = replaceAll(word,') ',':' );
        word = replaceAll(word,') :',':' );
        word = replaceAll(word,'):',':' );
        tuHaiBen = word.split(':');

        //視(shì)之(zhī)自(zì)然(rán)
        let hanBks = tuHaiBen[0].split(')');
        for(j=0;j<hanBks.length;j++){
          let hanbk = hanBks[j].split('(');
          chuHan += hanbk[0];
          bacKinh += hanbk[1];
          if(!bacKinh  || bacKinh == undefined)
            bacKinh = pinyin(chuHan);

          console.log('tuHaiBen',tuHaiBen,'hanbk',hanbk, 'chuHan',chuHan,'getHanViet(dict, chuHan)',getHanViet(dict, chuHan),'bacKinh',bacKinh);
        }

        tuMois += chuHan + '(' + getHanViet(dict, chuHan) + ' - ' + bacKinh + ') ' + tuHaiBen[1]  + '\n';
        //chuHans.push({"chuHan":tuHaiBen[1].trim(), "hanViet":getHanViet(dict, means[2].trim()), "bacKinh":means[3].trim(), "nghia":means[1].trim()});
      } catch (e) {
        console.log(word,means,e);
      }
    }

    this.setState({chinesse, tuMois });
  }

  render() {
      const {chuHans,chinesse,tuMois} = this.state;

      GA.trackPageView(`bt-convert`);
      // GA.trackEvent('TraTu', 'Han', word);
      return (
        <Layout {...this.props} navmenu={false} container={false}>
          <Container>
            <Row>
              <h2 className="h1Title">Tạo danh sách từ import</h2>
            </Row>
            <Row>
              <FormGroup style={{width:'100%'}}>
                <Label for="inputChuHan">Nhập nội dung chữ Hán</Label>
                <TextareaAutosize
                  className="txtAutoSize"
                  id="inputChuHan"
                  placeholder="Chữ Hán"
                  value={chinesse}
                  onChange={this.onChangeHandlerChuHan}
                />
              </FormGroup>
            </Row>
            <Col>
              <h4>Kết quả</h4>
              <span>Vui lòng click lên từng chữ để xem từ điển</span>
              <TextareaAutosize
                className="txtAutoSize"
                id="inputChuHan"
                placeholder="Chữ Hán"
                value={tuMois}
                />
            </Col>
          </Container>
        </Layout>
      )
  }
}
